html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

* {
  max-height: 1000000px; }

main,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  color: #1f1f1f;
  background: #fff;
  font: 16px/1.2 "Arial", "Helvetica Neue", "Helvetica", sans-serif;
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: none;
  -moz-osx-font-smoothing: grayscale;
  margin: 0; }
  @media (min-width: 768px) {
    body {
      font-size: 18px;
      line-height: 1.2; } }

img {
  max-width: 100%;
  height: auto;
  vertical-align: top; }

.gm-style img {
  max-width: none; }

@media (max-width: 1023px) {
  iframe {
    width: 100%; } }

.resize-active * {
  transition: none !important; }

ul,
ol,
dl,
p,
h1,
h2,
h3,
h4,
h5,
h6,
address,
form,
table,
blockquote,
applet,
embed,
object,
iframe,
frameset {
  margin: 0; }

blockquote {
  border-left: 4px solid #999;
  padding-left: 10px;
  margin-left: 0; }

table {
  border-collapse: collapse; }

th,
td {
  border: 1px solid #999;
  padding: 3px;
  text-align: left;
  vertical-align: top; }

th {
  text-align: center;
  vertical-align: middle; }

ul,
ol {
  padding-left: 0px; }

ul ul,
ul ol,
ol ul,
ol ol {
  padding-left: 20px;
  margin: 0; }

ul {
  list-style-type: disc; }

dl dt {
  float: left;
  clear: left;
  padding-right: 0.3em;
  font-weight: bold; }

dl dd {
  overflow: hidden; }

pre {
  max-width: 100%;
  overflow: auto; }

.nav-links,
.navigation,
.navigation-comments, .navigation-single {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 20px; }

.nav-links .next,
.nav-links .prev,
.navigation .next,
.navigation .prev,
.navigation-comments .next,
.navigation-comments .prev, .navigation-single .next a,
.navigation-single .prev a {
  background: #999;
  color: #1f1f1f;
  border: 1px solid transparent;
  padding: 10px 20px;
  font-size: 14px;
  line-height: 18px; }
  .nav-links .next:hover,
  .nav-links .prev:hover,
  .navigation .next:hover,
  .navigation .prev:hover,
  .navigation-comments .next:hover,
  .navigation-comments .prev:hover, .navigation-single .next a:hover,
  .navigation-single .prev a:hover {
    background: #1f1f1f;
    color: #fff; }

@media (min-width: 768px) {
  .content-column {
    display: flex; } }

@media (min-width: 768px) {
  #content {
    width: 70%; } }

#content:only-child {
  width: 100%; }

@media (min-width: 768px) {
  #sidebar {
    width: 30%;
